@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Signika:700,300,600);
:root {
  --red: #e74c3c;
  --red-dark: #c0392b;
  --orange: #e67e22;
  --orange-dark: #d35400;
  --green: #2ecc71;
  --green-dark: #27ae60;
  --teal: #1abc9c;
  --teal-dark: #16a085;
  --blue: #3498db;
  --blue-dark: #2980b9;
  --purple: #9b59b6;
  --purple-dark: #8e44ad;
  --nubo: #ecf0f1;
  --argxenta: #bdc3c7;
  --nokto: #34495e;
  --nokto-dark: #2c3e50;
  --primary: var(--teal);
  --primary-dark: var(--teal-dark);
  --secondary: var(--blue);
  --secondary-dark: var(--blue-dark);
  --dark: var(--nokto-dark);
  --light: #f8f9fa;
  --success: var(--green);
  --success-dark: var(--green-dark);
  --info: var(--purple);
  --info-dark: var(--purple-dark);
  --warning: var(--orange);
  --warning-dark: var(--orange-dark);
  --danger: var(--red);
  --danger-dark: var(--red-dark);
}

#root {
  height: 100% !important;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1abc9c;
  background-color: var(--primary);
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn {
  padding-top: 0.25rem;
  padding-bottom: 0.23rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

.btn-primary {
  color: white !important;
  background-color: #1abc9c;
  background-color: var(--primary);
  border-color: #1abc9c;
  border-color: var(--primary);
  --depth-color: var(--primary-dark);
}

.btn-secondary {
  background-color: #3498db;
  background-color: var(--secondary);
  border-color: #3498db;
  border-color: var(--secondary);
  --depth-color: var(--secondary-dark);
}

.btn:hover {
  background-color: var(--depth-color);
  border-color: var(--depth-color);
}

.btn-outline-secondary {
  color: #3498db;
  color: var(--secondary);
  border-color: #3498db;
  border-color: var(--secondary);
}

.btn-depth {
  border-width: 0px;
  border-bottom: 3px solid var(--depth-color);
}

.bg-primary {
  background-color: #1abc9c !important;
  background-color: var(--primary) !important;
}

.bg-primary-dark {
  background-color: #16a085 !important;
  background-color: var(--primary-dark) !important;
}

.navbar-brand a {
  color: #2c3e50;
  color: var(--dark);
}

.navbar-brand a:hover {
  text-decoration: none;
}

.nav-link {
  margin: 0.6rem;
}

.navbar-light .navbar-toggler {
  border-color: #00000000;
}

.navbar-dark .navbar-toggler {
  border-color: #00000000;
}

.navbar-nav .dropdown .dropdown-toggle {
  border: 2px lightgray solid;
  padding-left: 1rem;
  padding-right: 1rem;
}

.dropdown-menu {
  background-color: #f8f9fa;
  background-color: var(--light);
  border: 2px lightgray solid;
  border-radius: 0px;
  padding: 0px;
}

.dropdown-item {
  border-radius: 0px !important;
}

.dropdown-item:hover {
  background-color: #ecf0f1;
  background-color: var(--nubo);
}

.dropdown-item.active {
  background-color: lightgray;
  color: black;
}

.form-control {
  border: none;
}

.card {
  background-color: #f8f9fa;
  background-color: var(--light);
  border: none;
}

code {
  color: #9b59b6;
}

.justify {
  text-align: justify;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}

.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.lerniContainer {
  width: 100%;
  height: calc(100vh - 80px);
}

.lerniRespondo {
  border-bottom: 1rem solid;
  border-bottom-color: #fff0;
  transition: border-bottom 0.2s linear;
}

.lerniRespondo:hover span {
  -webkit-animation: bounce 0.5s;
          animation: bounce 0.5s;
}

.lerniRespondo:hover {
  border-bottom-color: #fff8;
  cursor: pointer;
  transition: border-bottom 0.2s linear;
}

.navbar {
  min-height: 80px;
  transition: min-height 0.3s linear, height 0.3s linear;
}

.lerniTeksto {
  color: white;
  text-align: center;
  /*text-shadow: 0 0 4px #0008;*/
  font-weight: bold;
  font: bold 7vw/1.6 'Signika', sans-serif;
  transition: font 0.3s linear;
}

.lerniDemando {
  pointer-events: none;
  text-shadow: 0 0 4px #0004;
  font: bold 10vw 'Signika', sans-serif;
  top: calc(50% - 10vw * 0.6);
}

.container {
  transition: max-width 0.3s linear;
}

@media only screen and (min-width: 768px) {
  .lerniTeksto {
    font: bold 6vw/1.6 'Signika', sans-serif;
  }
  .lerniDemando {
    font: bold 7vw 'Signika', sans-serif;
    top: calc(50% - 7vw * 0.6);
  }
}

@media only screen and (min-width: 1600px) {
  .lerniTeksto {
    font: bold 4vw/1.6 'Signika', sans-serif;
  }
  .lerniDemando {
    font: bold 5vw 'Signika', sans-serif;
    top: calc(50% - 5vw * 0.6);
  }
}

@media only screen and (max-width: 767px) {
  .lerniContainer {
    height: calc(100vh - 56px);
  }
  .navbar {
    min-height: 56px;
  }
  #lerniNavButton {
    margin: 0.6rem !important;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes bounce {
  0%,
  100% {
    -webkit-transform: translate(0);
            transform: translate(0);
  }
  25% {
    -webkit-transform: rotateX(20deg) translateY(2px) rotate(-3deg);
            transform: rotateX(20deg) translateY(2px) rotate(-3deg);
  }
  50% {
    -webkit-transform: translateY(-10px) rotate(3deg) scale(1.1);
            transform: translateY(-10px) rotate(3deg) scale(1.1);
  }
}

@keyframes bounce {
  0%,
  100% {
    -webkit-transform: translate(0);
            transform: translate(0);
  }
  25% {
    -webkit-transform: rotateX(20deg) translateY(2px) rotate(-3deg);
            transform: rotateX(20deg) translateY(2px) rotate(-3deg);
  }
  50% {
    -webkit-transform: translateY(-10px) rotate(3deg) scale(1.1);
            transform: translateY(-10px) rotate(3deg) scale(1.1);
  }
}

.lerniRespondo span:nth-child(2) {
  -webkit-animation-delay: 0.05s;
          animation-delay: 0.05s;
}
.lerniRespondo span:nth-child(3) {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}
.lerniRespondo span:nth-child(4) {
  -webkit-animation-delay: 0.15s;
          animation-delay: 0.15s;
}
.lerniRespondo span:nth-child(5) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}
.lerniRespondo span:nth-child(6) {
  -webkit-animation-delay: 0.25s;
          animation-delay: 0.25s;
}
.lerniRespondo span:nth-child(7) {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}
.lerniRespondo span:nth-child(8) {
  -webkit-animation-delay: 0.35s;
          animation-delay: 0.35s;
}
.lerniRespondo span:nth-child(9) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}
.lerniRespondo span:nth-child(10) {
  -webkit-animation-delay: 0.45s;
          animation-delay: 0.45s;
}
.lerniRespondo span:nth-child(11) {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.lerniRespondo span:nth-child(12) {
  -webkit-animation-delay: 0.55s;
          animation-delay: 0.55s;
}
.lerniRespondo span:nth-child(13) {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}
.lerniRespondo span:nth-child(14) {
  -webkit-animation-delay: 0.65s;
          animation-delay: 0.65s;
}
.lerniRespondo span:nth-child(15) {
  -webkit-animation-delay: 0.7s;
          animation-delay: 0.7s;
}

